<script>
import jsonData from '@/assets/local.json'
export default {
	name: 'HelloWorld',
	data() {
		return {
			myData: jsonData,
			key: [
				"Title",
				"Keywords",
				"Link",
				"Publication",
				"Date",
				"Notation",
				"Number of Electromechanical Actuators",
				"MAX Size (cm)",
				"Mass (g)",
				"Payload (g)",
				"Structural Simplicity (%) / Mass Integration",
				"MAX Speed (cm/s)",
				"MAX Duration (min) / MAX : MIN",
				"Multi-domain Energy Efficiency (%)",
				"Traversing Width",
				"Domain Crossing Speed (s)",
				"Weight-to-power (g/W)",
			],
			searchList: [],
			word: "",
			fieldWord: "",
			isUp: true,
		}
	},
	methods: {
		printTable() {
			// 创建打印窗口
			const printWindow = window.open('', '_blank');
			const printDocument = printWindow.document;

			// 写入打印内容
			printDocument.write(`
			        <html>
			          <head>
			            <title>Robot Publications and Links</title>
			            <style>
			              body {
			                font-family: Arial, sans-serif;
			                margin: 20px;
			              }
			              h1 {
			                text-align: center;
			                color: #333;
			                margin-bottom: 20px;
			              }
			              table {
			                width: 100%;
			                border-collapse: collapse;
			                margin-top: 20px;
			              }
			              th, td {
			                border: 1px solid #ddd;
			                padding: 12px;
			                text-align: left;
			              }
			              th {
			                background-color: #f2f2f2;
			                font-weight: bold;
			              }
			              tr:nth-child(even) {
			                background-color: #f9f9f9;
			              }
			              a {
			                color: #1a73e8;
			                text-decoration: none;
			              }
			              a:hover {
			                text-decoration: underline;
			              }
			              @media print {
			                body {
			                  margin: 0;
			                }
			                table {
			                  page-break-inside: auto;
			                }
			                tr {
			                  page-break-inside: avoid;
			                  page-break-after: auto;
			                }
			              }
			            </style>
			          </head>
			          <body>
			            <h1>Robot Publications and Links</h1>
			            <table>
			              <thead>
			                <tr>
			                  <th>Publication</th>
			                  <th>Link</th>
			                </tr>
			              </thead>
			              <tbody>
			                ${this.myData.map(item => `
			                  <tr>
			                    <td>${item.Publication}</td>
			                    <td><a href="${item.Link}">${item.Link}</a></td>
			                  </tr>
			                `).join('')}
			              </tbody>
			            </table>
			          </body>
			        </html>
			      `);

			// 关闭文档并触发打印
			printDocument.close();
			printWindow.focus();
			printWindow.print();
		},
		async downloadJSON() {
			// 将 JSON 对象转换为格式化的字符串
			const jsonContent = JSON.stringify(this.myData, null, 2);

			// 创建 Blob 对象
			const blob = new Blob([jsonContent], {
				type: 'application/json;charset=utf-8;'
			});

			// 创建下载链接
			const url = URL.createObjectURL(blob);
			const link = document.createElement('a');

			link.setAttribute('href', url);
			link.setAttribute('download', 'Cross-domain Robots.json'); // 设置下载文件名
			link.style.visibility = 'hidden';

			// 触发下载
			document.body.appendChild(link);
			link.click();
			document.body.removeChild(link);

			// 清理 URL 对象
			URL.revokeObjectURL(url);
		},
		async downloadPublicCSV() {
			try {
				// Base64 编码的 CSV 数据（这里只是一个示例，你需要替换成实际的 Base64 数据）
				const base64Data = '';

				// 解码 Base64 数据
				const byteCharacters = atob(base64Data);
				const byteArrays = [];

				for (let offset = 0; offset < byteCharacters.length; offset += 512) {
					const slice = byteCharacters.slice(offset, offset + 512);

					const byteNumbers = new Array(slice.length);
					for (let i = 0; i < slice.length; i++) {
						byteNumbers[i] = slice.charCodeAt(i);
					}

					const byteArray = new Uint8Array(byteNumbers);
					byteArrays.push(byteArray);
				}

				const blob = new Blob(byteArrays, { type: 'text/csv;charset=utf-8;' });
				const url = URL.createObjectURL(blob);
				const link = document.createElement('a');

				link.setAttribute('href', url);
				link.setAttribute('download', 'Cross-domain Robots.csv');
				link.style.visibility = 'hidden';

				document.body.appendChild(link);
				link.click();
				document.body.removeChild(link);

				// 清理 URL 对象
				URL.revokeObjectURL(url);
			} catch (error) {
				console.error('下载失败:', error);
			}
		},
		async copy() {
			try {
				// 将 myData 转换为 CSV 格式
				const headers = this.key.join(',');
				const rows = this.myData.map(item => {
					return Object.values(item).map(value => {
						// 处理包含逗号或引号的值
						if (typeof value === 'string' && (value.includes(',') || value.includes('"'))) {
							return `"${value.replace(/"/g, '""')}"`;
						}
						return value;
					}).join(',');
				});
				const csvContent = [headers, ...rows].join('\n');

				// 复制到剪贴板
				await this.$copyText(csvContent).then(() => {
					alert('复制成功');
				}).catch(() => {
					alert('复制失败');
				});
				console.log('CSV 数据已复制到剪贴板');
			} catch (error) {
				console.error('复制失败:', error);
				alert('复制失败');
			}
		},
		sortDescending(field, options = {}) {
			if (field == this.fieldWord) {
				this.isUp = !this.isUp;
			} else {
				this.fieldWord = field;
				this.isUp = true;
			}
			// 默认选项
			const defaultOptions = {
				// 是否区分大小写（仅字符串有效）
				caseSensitive: false,
				// 是否按数字排序（尝试将字符串转为数字）
				numeric: false
			};

			const config = {
				...defaultOptions,
				...options
			};

			if (!Array.isArray(this.myData) || this.myData.length === 0) {
				return [...(this.myData || [])];
			}

			// 创建新数组避免修改原数组
			const sortedList = [...this.myData];

			this.myData = sortedList.sort((a, b) => {
				// 获取字段值
				let valueA = a[field];
				let valueB = b[field];

				// 处理 undefined 或 null 值
				if (valueA == null && valueB == null) return 0;
				if (valueA == null) return 1; // null/undefined 排在最后
				if (valueB == null) return -1;

				// 如果启用数字排序，尝试转换为数字
				if (config.numeric) {
					const numA = Number(valueA);
					const numB = Number(valueB);

					// 如果都能成功转换为数字，则按数字比较
					if (!isNaN(numA) && !isNaN(numB)) {
						if (this.isUp) {
							return numB - numA; // 从大到小
						} else {
							return numA - numB; // 从大到小
						}
					}
				}

				// 字符串比较
				if (typeof valueA === 'string' && typeof valueB === 'string') {
					// 处理大小写
					const strA = config.caseSensitive ? valueA : valueA.toLowerCase();
					const strB = config.caseSensitive ? valueB : valueB.toLowerCase();

					// 从大到小排序
					if (strA < strB) return this.isUp ? 1 : -1;
					if (strA > strB) return this.isUp ? -1 : 1;
					return 0;
				}

				// 其他类型直接比较
				if (valueA < valueB) return this.isUp ? 1 : -1;
				if (valueA > valueB) return this.isUp ? -1 : 1;
				return 0;
			});
		},
		searchAndSort(event) {
			const searchTerm = event.target.value;

			// 如果搜索词为空，清空搜索结果
			if (!searchTerm || searchTerm.trim() === '') {
				this.searchList = [];
				return;
			}

			// 过滤数据，只保留包含精确匹配搜索词的项目
			const results = this.myData.filter(item => {
				// 检查每个字段是否包含精确匹配的搜索词
				return Object.values(item).some(value => {
					if (value === null || value === undefined) return false;
					// 转换为字符串并进行精确匹配
					return String(value).toLowerCase().includes(searchTerm.toLowerCase());
				});
			});

			this.searchList = results;
		},
		getItemValue(obj, path) {
			if (!obj || typeof obj !== 'object') return '';

			return path.split('.').reduce((current, key) => {
				return current && current[key] !== undefined ? current[key] : '';
			}, obj);
		},
		calculateMatchScore(text, search) {
			if (!text || !search) return 0;

			// 完全匹配得分最高
			if (text === search) return 100;

			// 开头匹配
			if (text.startsWith(search)) return 80;

			// 包含匹配
			if (text.includes(search)) {
				// 根据位置和长度计算分数
				const index = text.indexOf(search);
				const positionScore = Math.max(0, 50 - index * 2);
				const lengthScore = Math.min(30, (search.length / text.length) * 30);
				return 30 + positionScore + lengthScore;
			}

			// 部分字符匹配（模糊匹配）
			let matchCount = 0;
			let searchIndex = 0;

			for (let i = 0; i < text.length && searchIndex < search.length; i++) {
				if (text[i] === search[searchIndex]) {
					matchCount++;
					searchIndex++;
				}
			}

			if (matchCount > 0) {
				// 计算匹配比例
				const ratio = matchCount / search.length;
				return Math.min(20, Math.floor(ratio * 20));
			}

			return 0;
		}
	}
}
</script>

